#page-resume {
  font-size: smaller; }
  #page-resume .fa-print {
    position: relative;
    top: 20px; }
  #page-resume #avatar {
    border-radius: 50%;
    height: 7em;
    width: 7em;
    vertical-align: sub; }
  #page-resume h1 {
    font-size: 3.3em;
    margin-top: 0; }
  #page-resume #contact-info {
    margin-bottom: 1.5em; }
    #page-resume #contact-info div {
      display: inline-block; }
    #page-resume #contact-info .links {
      text-align: left; }
      #page-resume #contact-info .links div {
        display: block; }
    #page-resume #contact-info i {
      width: 2em;
      margin-left: 2em; }
    #page-resume #contact-info h2 {
      font-size: 1.6em;
      margin-left: 0.5em; }
  #page-resume .card p, #page-resume .card li {
    font-size: 0.9em; }
  #page-resume .card h2 {
    font-size: 1.5em;
    border-bottom: none;
    margin-top: 0;
    margin-left: 0; }
  #page-resume .card h3 {
    font-size: 1.1em;
    margin-bottom: 1em;
    margin-top: 0;
    border-bottom: 1px solid silver; }
  #page-resume .card h4 {
    font-weight: 600;
    font-size: 0.9em; }
  #page-resume .card p + h4, #page-resume .card p + h5 {
    margin-top: 0.5em; }
  #page-resume .card h5 {
    font-weight: 600;
    font-size: 0.77em; }
  #page-resume .card .keywords {
    display: block;
    font-size: 0.75em;
    margin: 5px 0 0 0; }
  #page-resume .card ul {
    margin-left: 1em;
    margin-bottom: 0; }
  #page-resume .card ul + h5 {
    margin-top: 1.5em; }

@media print {
  #page-resume {
    font-size: 16px; }
    #page-resume .fa-print {
      display: none; }
    #page-resume h1 {
      text-align: left;
      font-size: 2.5em; }
    #page-resume h2 {
      margin-top: 1em;
      margin-bottom: 1em;
      margin-left: 0; }
    #page-resume h3 {
      margin-top: 2em; }
    #page-resume .card {
      margin: 0 0 1em 0;
      padding: 0;
      background: none;
      filter: unset; }
    #page-resume a:after {
      content: ""; }
    #page-resume #avatar {
      display: none; }
    #page-resume #contact-info {
      width: 100%; }
      #page-resume #contact-info .links div {
        display: inline-block; }
      #page-resume #contact-info i {
        width: auto;
        margin-right: 5px;
        margin-left: 5px; }
    #page-resume blockquote {
      border: none; } }
