.thing h4 {
  font-size: 1.2em;
  margin-top: 0;
  margin-bottom: 12px; }

.thing p {
  font-size: 0.8em; }

.thing img {
  width: 3em;
  height: 4em;
  margin-right: 1em;
  float: left; }

ul {
  list-style: disc outside url("/image/bullet.gif");
  margin-left: 12px; }

#brand .brand, #intro .brand {
  opacity: 0; }

#header #brand img {
  display: none; }

#header.home #brand {
  display: none; }

#header.home > div {
  text-align: center; }

#header.home nav {
  float: none; }

#intro h3 {
  display: none; }

#doodles {
  margin: 0 auto;
  border: none; }
  #doodles td.next span {
    font-size: 2em;
    min-width: 80px;
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    min-height: 80px;
    border-radius: 50%; }
    #doodles td.next span:hover {
      background-color: rgba(0, 0, 0, 0.1); }
  #doodles td.prev span {
    font-size: 2em;
    border-radius: 50%;
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    min-width: 80px;
    min-height: 80px; }
    #doodles td.prev span:hover {
      background-color: rgba(0, 0, 0, 0.1); }

#doodle-gallery {
  list-style: none;
  padding: 0;
  margin: 0 auto; }
  #doodle-gallery li {
    text-align: center; }
    #doodle-gallery li span {
      display: block;
      margin: 5px; }
  #doodle-gallery .next, #doodle-gallery .prev {
    cursor: pointer;
    vertical-align: middle; }
  #doodle-gallery img {
    max-height: 95%;
    max-width: 95%; }

.imgbox {
  border: 1px solid silver;
  border-radius: 12px; }
